	module.exports = "<h2 id=overview>Overview</h2> <p>A popover is used to provide contextual information for another component on click. The popover itself is made up of two main elements: arrow and content. The content element follows the pattern of the popover box component, with a close icon in the top right corner, as well as a header and body. One of the directional modifiers (<code>.pf-m-left</code>, <code>.pf-m-top</code>, etc.) is required on the popover component</p> <h2 id=accessibility>Accessibility</h2> <table> <thead> <tr> <th>Attribute</th> <th>Applies to</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>role=&quot;dialog&quot;</code></td> <td><code>.pf-c-popover</code></td> <td>Identifies the element that serves as the popover container. <strong>Required</strong></td> </tr> <tr> <td><code>aria-labelledby=&quot;[id value of .pf-c-popover__header-title]&quot;</code></td> <td><code>.pf-c-popover</code></td> <td>Gives the popover an accessible name by referring to the element that provides the dialog title. <strong>Required when .pf-c-popover__header-title is present</strong></td> </tr> <tr> <td><code>aria-label=&quot;[title of popover]&quot;</code></td> <td><code>.pf-c-popover</code></td> <td>Gives the popover an accessible name. <strong>Required when .pf-c-popover__header-title is <em>not</em> present</strong></td> </tr> <tr> <td><code>aria-describedby=&quot;[id value of applicable content]&quot;</code></td> <td><code>.pf-c-popover</code></td> <td>Gives the popover an accessible description by referring to the popover content that describes the primary message or purpose of the dialog. Not used if there is no static text that describes the popover.</td> </tr> <tr> <td><code>aria-modal=&quot;true&quot;</code></td> <td><code>.pf-c-popover</code></td> <td>Tells assistive technologies that the windows underneath the current popover are not available for interaction. <strong>Required</strong></td> </tr> <tr> <td><code>aria-label=&quot;Close&quot;</code></td> <td><code>.pf-c-popover__close .pf-c-button</code></td> <td>Provides an accessible name for the close button as it uses an icon instead of text. <strong>Required</strong></td> </tr> </tbody> </table> <h2 id=usage>Usage</h2> <table> <thead> <tr> <th>Class</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>.pf-c-popover</code></td> <td><code>&lt;div&gt;</code></td> <td>Creates a popover. Always use it with a modifier class that positions the popover relative to the element that triggered it. <strong>Required</strong></td> </tr> <tr> <td><code>.pf-c-popover__arrow</code></td> <td><code>&lt;div&gt;</code></td> <td>Creates an arrow pointing towards the element the popover describes. <strong>Required</strong></td> </tr> <tr> <td><code>.pf-c-popover__content</code></td> <td><code>&lt;div&gt;</code></td> <td>Creates the body of the popover. <strong>Required</strong></td> </tr> <tr> <td><code>.pf-c-popover__close</code></td> <td><code>&lt;div&gt;</code></td> <td>Contains and positions the close icon in the top-right corner of the popover. <strong>Required</strong></td> </tr> <tr> <td><code>.pf-c-popover__header</code></td> <td><code>&lt;header&gt;</code></td> <td>The header text area of the popover.</td> </tr> <tr> <td><code>.pf-c-popover__header-title</code></td> <td><code>&lt;h1&gt;</code>,<code>&lt;h2&gt;</code>,<code>&lt;h3&gt;</code>,<code>&lt;h4&gt;</code>,<code>&lt;h5&gt;</code>,<code>&lt;h6&gt;</code></td> <td>The actual popover title.</td> </tr> <tr> <td><code>.pf-c-popover__body</code></td> <td><code>&lt;div&gt;</code></td> <td>The popover&#39;s body text. <strong>Required</strong></td> </tr> <tr> <td><code>.pf-m-left</code></td> <td><code>.pf-c-popover</code></td> <td>Positions the popover to the left of the element.</td> </tr> <tr> <td><code>.pf-m-right</code></td> <td><code>.pf-c-popover</code></td> <td>Positions the popover to the right of the element.</td> </tr> <tr> <td><code>.pf-m-top</code></td> <td><code>.pf-c-popover</code></td> <td>Positions the popover to the top of the element.</td> </tr> <tr> <td><code>.pf-m-bottom</code></td> <td><code>.pf-c-popover</code></td> <td>Positions the popover to the bottom of the element.</td> </tr> </tbody> </table> ";

